
.navbar {
    &-wrap {
        display: flex;
        align-items: center;
        margin-top: 30px;    
        background-color: #000;
    }   

}

.logo {
    &__text {
        display: block;
        font-family: 'Post-No-Bills-Jaffna-SemiBold';
        font-weight: 600;
        font-size: 36px;
        line-height: 54px;
        margin-bottom: 15px;
    }
    &__desc {
        display: block;
        font-family: 'Raleway';
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.12em;
    }
}
 

.menu {
    &__list {
        margin: 0;
        display: flex;
    }
    &__item {
        list-style: none;
        margin-right: 20px;
        &:last-child {
            margin-right: 0;
        }
    }
    &__link {
        @body_font;
        text-decoration: none;
        
    }
}

